.Preloader_water__2N2ZC:before, .Preloader_water__2N2ZC:after{
    content:'';
    position: absolute;
    width:400px;
    height: 400px;
    top:-150px;
    background-color: #fff;
}
.Preloader_water__2N2ZC:before{
    border-radius: 45%;
    background:rgba(255,255,255,.7);
    animation:Preloader_wave__1rgoN 5s linear infinite;
}
.Preloader_water__2N2ZC:after{
    border-radius: 35%;
    background:rgba(255,255,255,.3);
    animation:Preloader_wave__1rgoN 5s linear infinite;
}
@keyframes Preloader_wave__1rgoN{
    0%{
        transform: rotate(0);
    }
    100%{
        transform: rotate(360deg);
    }
}

